import React, { useState } from 'react';
import classes from './MyModalNavigation.module.css';
import logo from '../../UI/icons/nicedunkslogo.png'

const MyModalNavigation = ({ visible, setVisible }) => {
 
  const rootClasses = [classes.myModalNavigation];
  const [activeMenu, setActiveMenu] = useState('');

  const toggleMenu = (menuId) => {
    setActiveMenu((prevState) => (prevState === menuId ? '' : menuId));
  };

  const handleClick = () => {
    setVisible(false);
  };

  if (visible) {
    rootClasses.push(classes.active);
  }


  return (
    <div className={rootClasses.join(' ')}>
      <div className={classes.myModalNavigation__overlay} onClick={handleClick} />
      <div className={classes.myModalNavigation__line} style={{marginTop: 93}}></div>
      <div className={classes.myModalNavigation__title}></div>
      <div className={classes.myModalNavigation__line} style={{marginBottom: 15}}></div>
      <div className={classes.myModalNavigation__link} onClick={() => window.location.href = '/all%20products?'}> </div>
      <div className={`${classes.myModalNavigation__link} ${classes.purple}`} onClick={() => window.location.href = '/all%20products?category=used'}>/ </div>
      <div className={classes.myModalNavigation__link} onClick={() => window.location.href = '/all%20products?category=accessories'}></div>
      <div className={classes.myModalNavigation__link} onClick={() => window.location.href = '/all%20products?category=clothes'}></div>
      <div className={classes.myModalNavigation__link}></div>
      <div className={`${classes.myModalNavigation__link} ${classes.purple}`} onClick={() => window.location.href = '/all%20products?category=preorder'}></div>
      <div className={classes.myModalNavigation__link} onClick={() => window.location.href = '/all%20products?category=newSneakers'}> </div>
      <div className={classes.myModalNavigation__line} style={{marginTop: 10}}></div>
      <div className={classes.myModalNavigation__title}> </div>
      <div className={classes.myModalNavigation__line} style={{marginBottom: 15}}></div>
      <div className={classes.myModalNavigation__accordion}>
      <ul>
        <li
          className={`${classes.myModalNavigation__link} ${
            activeMenu === 'delivery' ? classes.open : ''
          }`}
          onClick={() => toggleMenu('delivery')}
        >
            
        </li>
        <li
          className={`${classes.myModalNavigation__content} ${
            activeMenu === 'delivery' ? classes.open : ''
          }`}
        >
          <br />         <br />
          <br />     :
          <br /> Go   <br />
          <br />       : 
          <br />,    Boxberry  <br />
          <br />    <br />
          <br />      EMS  <br />
          <br />   <br /><br />
        </li>
      </ul>
      <ul>
        <li
          className={`${classes.myModalNavigation__link} ${
            activeMenu === 'contacts' ? classes.open : ''
          }`}
          onClick={() => toggleMenu('contacts')}
        >
          
        </li>
        <li
          className={`${classes.myModalNavigation__content} ${
            activeMenu === 'contacts' ? classes.open : ''
          }`}
        >
          <br/><a href="https://t.me/NiceDunksOrder">  </a><br />
          <br/><a href="https://www.instagram.com/_goba_/">   </a><br />
          <br/><a href="https://vk.com/nicedunks">   </a><br />
          <br/><a href="https://www.avito.ru/user/c560662529d1530f9fab8dd500a9934e/profile?src=sharing">   </a><br />
          <br/><a href="https://instagram.com/donalddunks/">    </a><br />
          <br/><a href="https://t.me/NiceDunksManager">  </a><br /><br /><br />
        </li>
      </ul>
      <ul>
        <li
          className={`${classes.myModalNavigation__link} ${
            activeMenu === 'faq' ? classes.open : ''
          }`}
          onClick={() => toggleMenu('faq')}
        >
           - 
        </li>
        <li
          className={`${classes.myModalNavigation__content} ${
            activeMenu === 'faq' ? classes.open : ''
          }`}
        >
          <br /><p>   ?</p>,     .     .<br /><br />
          <p>   ?</p>     2017    <a href="https://vk.com/wall170701681_4583"></a>   <a href="https://www.avito.ru/user/c560662529d1530f9fab8dd500a9934e/profile?src=sharing"></a>.<br /><br />
          <p>     ?</p>      14-21 .   .<br /><br />
          <p> ,      ?</p>     ,    .      <a href="https://t.me/NiceDunksManager"></a>.<br /><br />
        </li>
      </ul>
      <ul>
        <li
          className={`${classes.myModalNavigation__link} ${
            activeMenu === 'telegram' ? classes.open : ''
          }`}
          onClick={() => toggleMenu('telegram')}
        >
           
        </li>
        <li style={{marginBottom: 10}}
          className={`${classes.myModalNavigation__content} ${
            activeMenu === 'telegram' ? classes.open : ''
          }`}
        >
          <br />  <a href="https://t.me/NiceDunks"> </a>      <br />     ,      <br /> - . <br /><br />       ,   <br />     ,     . <br /> 
        </li>
      </ul>
      <div className={classes.myModalNavigation__line}></div>
      <div className={classes.myModalNavigation__logo}>
        <img src={logo} alt=" NiceDunks" />
      </div>
      </div>
    </div>
  );
}

export default MyModalNavigation;